import { FC, useCallback, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';

// Components
import DateInput, { DateInputProps } from '../DateInput';

// Styles
import './DateRangeInput.scss';

// Types
import { TestMetadata } from 'types';

export interface DateRangeInputProps {
  /** Class name for component container */
  containerClassName?: string;
  /** If label is not visible, use aria-label for screen readers */
  startDatelabelText?: string;
  /** start date container class name */
  startDateContainerClassName: string;
  /** If label is not visible, use aria-label for screen readers */
  endDatelabelText?: string;
  /** end date container class name */
  endDateContainerClassName: string;
  /** control selection event */
  onDateChange?: (startDate?: Date, endDate?: Date) => void;
  // External start date value for the component
  selectedStartDate?: Date;
  // External end datevalue for the component
  selectedEndDate?: Date;
  /** If flag set to true, on date change start/end date is cleared based on the selection  */
  enforceValidDateRange?: boolean;
  /** Determines what kind of orientation will have the children of the card - stacked (default), inline */
  orientation: 'stacked' | 'inline';
  /**Props to pass to both date inputs */
  inputDateProps: DateInputProps;
  /**Props to pass to both date inputs */
  startDateProps?: DateInputProps;
  /**Props to pass to both date inputs */
  endDateProps?: DateInputProps;
  /** Sets test id */
  testMetadata?: {
    startDate?: Record<string, TestMetadata>;
    endDate?: Record<string, TestMetadata>;
  };
}

const DateRangeInput: FC<DateRangeInputProps> = ({
  containerClassName,
  startDatelabelText,
  endDatelabelText,
  selectedStartDate,
  selectedEndDate,
  onDateChange,
  enforceValidDateRange = true,
  orientation,
  startDateContainerClassName,
  endDateContainerClassName,
  inputDateProps,
  testMetadata,
  startDateProps,
  endDateProps,
  ...otherProps
}) => {
  const dateRangeInputClassNames = useMemo(
    () =>
      classNames(
        'sb-daterange-input',
        {
          'sb-daterange-input_inline': orientation === 'inline',
          'sb-daterange-input_stacked': orientation === 'stacked',
        },
        containerClassName,
      ),
    [containerClassName, orientation],
  );

  const [startDate, setStartDate] = useState(selectedStartDate);
  const [endDate, setEndDate] = useState(selectedEndDate);

  useEffect(() => {
    setStartDate(selectedStartDate);
  }, [selectedStartDate]);

  useEffect(() => {
    setEndDate(selectedEndDate);
  }, [selectedEndDate]);

  const onStartDateChange = useCallback(
    (date?: Date) => {
      const start = date;
      let end = endDate;
      if (
        enforceValidDateRange &&
        date &&
        endDate &&
        date.getTime() > endDate.getTime()
      ) {
        end = undefined;
      }
      setStartDate(start);
      setEndDate(end);
      onDateChange?.(start, end);
    },
    [endDate, enforceValidDateRange, onDateChange],
  );

  const onEndDateChange = useCallback(
    (date?: Date) => {
      setEndDate(date);
      onDateChange?.(startDate, date);
    },
    [startDate, onDateChange],
  );

  return (
    <div {...otherProps} className={dateRangeInputClassNames}>
      <DateInput
        {...inputDateProps}
        {...startDateProps}
        containerClassName={startDateContainerClassName}
        selected={startDate}
        onDateChange={onStartDateChange}
        selectsStart={true}
        startDate={startDate}
        endDate={endDate}
        labelText={startDatelabelText}
        calendarClassName="daterange-input"
        testMetadata={{ ...testMetadata?.startDate }}
      />
      <DateInput
        {...inputDateProps}
        {...endDateProps}
        containerClassName={endDateContainerClassName}
        selected={endDate}
        onDateChange={onEndDateChange}
        selectsEnd
        startDate={startDate}
        endDate={endDate}
        minDate={startDate}
        labelText={endDatelabelText}
        calendarClassName="daterange-input"
        testMetadata={{ ...testMetadata?.endDate }}
      />
    </div>
  );
};

export default DateRangeInput;
