import {
  Control,
  Controller,
  FieldValues,
  Path,
  RegisterOptions,
} from 'react-hook-form';
import classNames from 'classnames';

// Components
import FormField, { FormFieldProps } from '../FormField';
import DateRangeInput from '../DateRangeInput';
import FormReadOnlyFieldDateRangeValue from '../FormReadOnlyFieldDateRangeValue';

// Styles
import './FormDateRangeField.scss';

// Types
import { DateInputProps } from 'DateInput';
import { TestMetadata } from 'types';

export type FormDateRangeValue = {
  startDate?: Date;
  endDate?: Date;
};

export interface FormDateRangeFieldProps<I extends Record<string, unknown>> {
  /** Class name */
  className?: string;
  /** Disable field (greyed out) */
  disabled?: boolean;
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Sets helper */
  helper?: string;
  /* Label for override */
  labelFor?: string;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Mark field required (*) */
  required?: boolean;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    dateRangeInput?: {
      startDate?: Record<string, TestMetadata>;
      endDate?: Record<string, TestMetadata>;
    };
  };
  /** Rules passed down to control */
  rules?: RegisterOptions;
  /** Format of the date */
  dateFormat?: string;
  defaultValue?: FormDateRangeValue;
  orientation?: 'stacked' | 'inline';
  inputDateProps?: DateInputProps;
  startDateProps?: DateInputProps;
  endDateProps?: DateInputProps;
  startDatelabelText?: string;
  startDateContainerClassName?: string;
  endDatelabelText?: string;
  endDateContainerClassName?: string;
}

const FormDateRangeField = <I extends Record<string, unknown>>({
  className,
  disabled,
  fieldName,
  helper,
  labelFor,
  labelText,
  tooltip,
  required,
  testMetadata,
  rules,
  dateFormat,
  defaultValue,
  inputDateProps,
  orientation = 'inline',
  startDatelabelText,
  startDateContainerClassName = '',
  endDatelabelText,
  endDateContainerClassName = '',
  startDateProps,
  endDateProps,
  readOnly,
  ...otherProps
}: FormDateRangeFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  const isRequired = (value: FormDateRangeValue): string | undefined => {
    if (required && (!value?.startDate || !value?.endDate))
      return `${labelText} is required.`;
  };
  const formDateRangeFieldClassNames = classNames(
    'sb-date-range-field',
    className,
  );
  return (
    <FormField<I>
      className={formDateRangeFieldClassNames}
      fieldName={fieldName}
      helper={helper}
      labelFor={labelFor}
      labelText={labelText}
      tooltip={tooltip}
      required={required}
      readOnly={readOnly}
      control={(formMethods, props) => (
        <Controller
          control={formMethods.control as Control<FieldValues>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          rules={{
            validate: {
              required: isRequired,
              ...rules?.validate,
            },
            ...rules,
          }}
          render={({ field: { onChange, value, name } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldDateRangeValue
                  startDate={value?.startDate}
                  endDate={value?.endDate}
                  testMetadata={{
                    startDate: testMetadata?.dateRangeInput?.startDate?.input,
                    endDate: testMetadata?.dateRangeInput?.endDate?.input,
                    emptyValue: {
                      'data-testid': `FormGenerator__DateRangeField_${name}_Empty`,
                    },
                  }}
                />
              );
            }

            return (
              <DateRangeInput
                inputDateProps={{
                  disabled,
                  dateFormat,
                  placeholderText: dateFormat,
                  ...props,
                  ...inputDateProps,
                }}
                startDateProps={{
                  ...startDateProps,
                }}
                endDateProps={endDateProps}
                selectedStartDate={
                  value?.startDate
                    ? typeof value?.startDate === 'string'
                      ? new Date(Date.parse(value?.startDate))
                      : value?.startDate
                    : null
                }
                startDatelabelText={startDatelabelText}
                startDateContainerClassName={startDateContainerClassName}
                selectedEndDate={
                  value?.endDate
                    ? typeof value?.endDate === 'string'
                      ? new Date(Date.parse(value?.endDate))
                      : value?.endDate
                    : null
                }
                endDatelabelText={endDatelabelText}
                endDateContainerClassName={endDateContainerClassName}
                orientation={orientation}
                onDateChange={(startDate, endDate) =>
                  onChange({ startDate, endDate })
                }
                testMetadata={{ ...testMetadata?.dateRangeInput }}
              />
            );
          }}
          name={fieldName as string}
        />
      )}
      {...testMetadata?.wrapper}
      {...otherProps}
    />
  );
};

export default FormDateRangeField;
